<template>
  <!--<div>-->
    <!--<Button to="/index" style="margin-left: 0;">首页</Button>-->
    <!--<Button to="/store" style="margin-left:25px;">订单</Button>-->
    <!--<Button to="/search" style="margin-left: 25px;">查询</Button>-->
    <!--<Button to="/home" style="margin-left: 25px;">我的</Button>-->
    <!---->
  <!--</div>-->
  <footer class="footer" style="position: absolute;bottom:0;width: 95%;">

    <div  v-for ="item in nav"  @click="selectNav(item.name)" >
      <router-link :to="{name:isSelect}">
        <!--到当前页面-->
        <div class="icon"> </div>
        <!--//判断，当isSelect是当前页面的name时图标为active-->
        <span :class="isSelect === item.name ? 'active' : ''">{{item.title}}</span>
        <!--//判断，当isSelect是当前页面的name时文字添加class=active-->
      </router-link>
    </div>
  </footer>


</template>

<script>
    export default {
        name: "footer",
      data () {
        return {
        isSelect:'',
        nav:[
          {title:'首页',name:'index'},
          {title:'订单', name:"store", },
          {title:'查询', name:"search", },
          {title:'我的', name:"home", }
        ]
        }
        }
      ,

      mounted(){ this.isSelect = sessionStorage.getItem('isSelect');   //sessionStrorage(会话储存)获取之前储存的内容
        if(this.$route.name ==="index" || this.$route.name === 'store' || this.$route.name === 'search' || this.$route.name === 'home'){
          this.selectNav(this.$route.name);
          console.log(this.$route.name)
          //当前页面为index或者store或者search或者home时执行获取当前url的name
        }
      },
      beforeRouteLeave(){this.selectNav('my')},
      methods:{
        selectNav(title){
          var isThis = this;
          isThis.isSelect = title; //当点击或啥返回上一页时都可以获取当前的名称
          switch (title) {
            case 'index':this.$router.push('/index');
              break;
            case 'store':this.$router.push('/store');
              break;
            case 'search':this.$router.push('/search');
              break;
            case 'home':this.$router.push('/home');
              break;
          }
          sessionStorage.setItem('isSelect',this.isSelect);  //sessionStorage(会话储存)储存名称为isSelect的变量  this.isSelect为值
        }
      }





    }
</script>

<style scoped>
  .footer{ background:white; height:2.5rem;

  }
  .footer>div{float:left; align-items:center; box-shadow: 1px 1px #aaa;
    margin-left: 15px; width: 50px;height: 25px;background-color: #cbfff7
  }
  /*border-right:1px solid #505050;border-bottom:1px solid #505050;border-left:1px solid white;border-top:1px solid white;*/
  .footer > div a{  justify-content:center; align-items:center; flex-direction:row; color: #23444a;font-family: 华文琥珀}
  /*.footer>div:last-child{border-right:0}*/

  .footer > div a:hover{  justify-content:center; align-items:center; flex-direction:row; color: #0922ff;}

  .active{ color: #e35b11
  }
</style>



